.navbar {
    width: 100%;
    margin-top: 20px;
    background: rgb(182, 179, 179);
    min-height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar__link, a {
    margin-right: 10px;
}

@media (max-width: 750px) {
    .navbar__link {
        margin: 0;
    }
}

@media (max-width: 750px) {
    .navbar {
        padding: 10px;
    }
}